@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body {
  font-family: "Roboto", sans-serif;
  font-size: 5em;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #000;
  color: #fff;
  text-shadow: 4px 3px 1px black;
}

span {
  display: inline-block;
  opacity: 0;
  transform: translateY(75px);
  transition: all 0.4s ease;
}

.fade {
  color: #ff9800c7;
  transform: translateY(0px);
  opacity: 1;
}

.heart {
  background: red;
  position: absolute;
  height: 250px;
  width: 250px;
  transform: rotate(-45deg);
  animation: pulse 1.25s linear infinite;
}

.heart::after {
  background: inherit;
  border-radius: 50%;
  content: "";
  position: absolute;

  top: -50%;

  left: 0;
  height: 250px;
  width: 250px;
}
.heart::before {
  background: inherit;
  border-radius: 50%;
  content: "";
  position: absolute;
  top: 0;
  right: -50%;
  height: 250px;
  width: 250px;
}

@keyframes pulse {
  0% {
    transform: rotate(-45deg) scale(1);
  }
  50% {
    transform: rotate(-45deg) scale(1.3);
  }
  100% {
    transform: rotate(-45deg) scale(1);
  }
}
